
<div
  class="vdp-datepicker__date-input"
  :class="{ 'input-group': bootstrapStyling }"
>
  <!-- Calendar Button -->
  <button
    v-if="calendarButton"
    type="button"
    class="vdp-datepicker__calendar-button"
    :class="({ 'input-group-prepend': bootstrapStyling }, { disabled })"
    @click="showCalendar"
  >
    <slot name="calendarButton">
      <span :class="{ 'input-group-text': bootstrapStyling }">
        <i :class="calendarButtonIcon">
          {{ calendarButtonIconContent }}
          <span v-if="!calendarButtonIcon">&hellip;</span>
        </i>
      </span>
    </slot>
  </button>
  <!-- Input -->
  <input
    :id="id"
    :ref="refName"
    :type="inline ? 'hidden' : 'text'"
    :class="computedInputClass"
    :name="name"
    :value="formattedValue"
    :open-date="openDate"
    :placeholder="placeholder"
    :clear-button="clearButton"
    :disabled="disabled"
    :required="required"
    :readonly="!typeable"
    autocomplete="off"
    @click="showCalendar"
    @keyup="parseTypedDate"
    @blur="inputBlurred"
  />
  <!-- Clear Button -->
  <button
    v-if="clearButton && selectedDate"
    type="button"
    class="vdp-datepicker__clear-button"
    :class="{ 'input-group-append': bootstrapStyling }"
    @click="clearDate()"
  >
    <slot name="clearButton">
      <span :class="{ 'input-group-text': bootstrapStyling }">
        <i :class="clearButtonIcon">
          <span v-if="!clearButtonIcon">&times;</span>
        </i>
      </span>
    </slot>
  </button>
  <slot name="afterDateInput"></slot>
</div>
