
<div
  class="vdp-datepicker"
  :class="[wrapperClass]"
  :data-rtl="isRtl || null"
  :data-inline="isInline || null"
  :data-min-height="ensureMinHeight || null"
>
  <DateInput
    :id="id"
    :selectedDate="selectedDate"
    :resetTypedDate="resetTypedDate"
    :format="format"
    :translation="translation"
    :inline="inline"
    :name="name"
    :refName="refName"
    :openDate="openDate"
    :placeholder="placeholder"
    :inputClass="inputClass"
    :typeable="typeable"
    :clearButton="clearButton"
    :clearButtonIcon="clearButtonIcon"
    :calendarButton="calendarButton"
    :calendarButtonIcon="calendarButtonIcon"
    :calendarButtonIconContent="calendarButtonIconContent"
    :disabled="disabled"
    :required="required"
    :bootstrapStyling="bootstrapStyling"
    :useUtc="useUtc"
    @showCalendar="showCalendar"
    @closeCalendar="close"
    @typedDate="setTypedDate"
    @clearDate="clearDate"
  >
    <slot slot="afterDateInput" name="afterDateInput"></slot>
    <slot slot="clearButton" name="clearButton"></slot>
  </DateInput>

  <div class="vdp-datepicker__container">
    <div class="vdp-datepicker__picker">
      <!-- Day View -->
      <PickerDay
        v-if="allowedToShowView('day')"
        :value="value"
        :dayFormatter="dayFormatter"
        :headlineDayFormatter="headlineDayFormatter"
        :prevDisabled="prevDisabled"
        :nextDisabled="nextDisabled"
        :pageDate="pageDate"
        :selectedDate="selectedDate"
        :showDayView="showDayView"
        :fullMonthName="fullMonthName"
        :allowedToShowView="allowedToShowView"
        :disabledDates="disabledDates"
        :highlighted="highlighted"
        :calendarClass="calendarClass"
        :translation="translation"
        :pageTimestamp="pageTimestamp"
        :isRtl="isRtl"
        :mondayFirst="mondayFirst"
        :dayCellContent="dayCellContent"
        :useUtc="useUtc"
        :daysCustomDisplay="daysCustomDisplay"
        @changedMonth="handleChangedMonthFromDayPicker"
        @selectDate="selectDate"
        @showMonthCalendar="showMonthCalendar"
        @selectedDisabled="selectDisabledDate"
      >
        <slot slot="beforeCalendarHeader" name="beforeCalendarHeader"></slot>
        <slot slot="prevButton" name="prevButton"><IconLeft /></slot>
        <slot slot="nextButton" name="nextButton"><IconRight /></slot>
      </PickerDay>

      <!-- Month View -->
      <PickerMonth
        v-if="allowedToShowView('month')"
        :value="value"
        :monthFormatter="monthFormatter"
        :headlineMonthFormatter="headlineMonthFormatter"
        :prevDisabled="prevDisabled"
        :nextDisabled="nextDisabled"
        :pageDate="pageDate"
        :selectedDate="selectedDate"
        :showMonthView="showMonthView"
        :allowedToShowView="allowedToShowView"
        :disabledDates="disabledDates"
        :calendarClass="calendarClass"
        :translation="translation"
        :isRtl="isRtl"
        :useUtc="useUtc"
        :monthsCustomDisplay="monthsCustomDisplay"
        @selectMonth="selectMonth"
        @showYearCalendar="showYearCalendar"
        @changedYear="setPageDate"
      >
        <slot slot="beforeCalendarHeader" name="beforeCalendarHeader"></slot>
        <slot slot="prevButton" name="prevButton"><IconLeft /></slot>
        <slot slot="nextButton" name="nextButton"><IconRight /></slot>
      </PickerMonth>

      <!-- Year View -->
      <PickerYear
        v-if="allowedToShowView('year')"
        :value="value"
        :yearFormatter="yearFormatter"
        :headlineYearFormatter="headlineYearFormatter"
        :prevDisabled="prevDisabled"
        :nextDisabled="nextDisabled"
        :pageDate="pageDate"
        :selectedDate="selectedDate"
        :showYearView="showYearView"
        :allowedToShowView="allowedToShowView"
        :disabledDates="disabledDates"
        :calendarClass="calendarClass"
        :translation="translation"
        :isRtl="isRtl"
        :useUtc="useUtc"
        @selectYear="selectYear"
        @changedDecade="setPageDate"
      >
        <slot slot="beforeCalendarHeader" name="beforeCalendarHeader"></slot>
        <slot slot="prevButton" name="prevButton"><IconLeft /></slot>
        <slot slot="nextButton" name="nextButton"><IconRight /></slot>
      </PickerYear>
    </div>
  </div>
</div>
